.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 2rem .5rem;
  height: 100vh;
  background-image: linear-gradient(to right top, #072757, #00266a, #00227b, #001b8a, #210897);
}

h2,
h3 {
  color: #ffb700ef;
}

h3 {
  font-weight: 400;
  margin-top: .8rem;
}


.container__content {
  padding: 1rem;
  color: #000000;
  background-color: #ffffff;
  width: 85%;
  height: 60%;
  border-radius: 8px;
  box-shadow: 10px 10px 16px 0px rgba(0,0,0,0.75);
}